<template>
    <div class="footer">
        <ul>
            <router-link tag='li'   :to="{path:'/Home',query:{geohash:geohash}}" exact>
                <i class="iconfont iconeliaomo"></i>
                <p>外卖</p>
            </router-link>
            <router-link tag='li'   to='/Search'>
                <i class="iconfont icon09"></i>
                <p>搜索</p>
            </router-link>
            <router-link tag='li'    to='/Order'>
                <i class="iconfont iconicon--copy-copy"></i>
                <p>订单</p>
            </router-link>
            <router-link tag='li'   to='/User' >
                <i class="iconfont iconwodedangxuan"></i>
                <p>我的</p>
            </router-link>
        </ul>
    </div>
</template>

<script>
export default {
  data() {
    return {
      geohash:0
    };
  },
  methods: {
  },
  created() {
    if (localStorage.getItem("geta")) {
      var arr = JSON.parse(localStorage.getItem("geta"));
      for (let i in arr) {
        this.geohash = arr[i].geohash;
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.footer {
  width: 100%;
  height: 0.96rem;
  background: white;
  position: fixed;
  bottom: 0;
  ul {
    width: 100%;
    height: 100%;
    display: flex;
    li {
      width: 25%;
      height: 100%;
      i {
        font-size: 0.42rem;
        color: gray;
        display: block;
        margin: 0 auto;
        margin-top: 0.12rem;
      }
      p {
        font-size: 0.22rem;
        margin-top: 0.02rem;
      }
    }
    .router-link-active {
      i {
        color: #0698FF;
      }
    }
  }
}
</style>



