<template>
    <div class="head">
        <!-- 头部左侧返回键位置 -->
        <p class="logo" v-if="!headback&&!headsearch">ele.me</p>
        <router-link tag='i'  class="el-icon-search" to='/Search' v-if="headsearch"></router-link>
        <i class='el-icon-arrow-left' v-if="headback&&!bol2&&!bol3" @click="$router.go(-1)"></i>
        <router-link tag='i' to='/Registerok' class='el-icon-arrow-left' v-if="headback&&bol2&&!bol3"></router-link>
        <router-link tag='i' to='/User' class='el-icon-arrow-left' v-if="headback&&!bol2&&bol3"></router-link>
        <!-- 头部中间标题 -->
        <span class="s1" v-if='headback&&!headto'>{{where}}</span>
        <router-link tag='span' v-if='!headback&&!headbacks' class="s1" to='/City'>{{names}}</router-link>
        <p class="s3" v-if='headto&&!bol2' v-html="bol"></p>
        <p class="s4" v-if="bol2" >编辑地址</p>
        <!-- 头部右侧登录注册部分 -->
        <router-link v-if="!headback&&users" tag='div' to='/Register'>登录 | 注册</router-link>
        <router-link v-if="!users&&!headto&&!headback" tag='i' to='/User' class="iconfont iconwodedangxuan" ></router-link>
        <router-link class="s2" v-if="headback&&!headto" tag='span' to="/City">切换城市</router-link>
        <span v-if="bol2" class="edit" @click="getadd()"><slot v-if="bol1" name='edit'>编辑</slot><slot v-if="!bol1" @click="getni()"  name='edit'>完成</slot></span>
    </div>
</template>

<script>
export default {
  data() {
    return {
      where: "",
      names: "",
      bol1:true,
      users:true
    };
  },
  mounted() {
    let where = this.$route.params.name;
    this.where = where;
    if (localStorage.getItem("geta")) {
      var arr = JSON.parse(localStorage.getItem("geta"));
      for (let i in arr) {
        this.names = arr[i].name;
      }
    }
      console.log( this.$store.state.users)
      if(this.$store.state.users.length<1){
        this.users=true
      }else {
        this.users=false
      }
  },
  props: ["headback", "headsearch", "headgo", "headto", "bol",'headbacks','bol2','bol3'],
  methods: {
    getadd(){
      this.bol1=!this.bol1
      this.$emit('add',this.bol1)
    },
  }
};
</script>

<style lang="scss" scoped>
.head {
  width: 7.5rem;
  height: 0.88rem;
  background: #3792e5;
  display: flex;
  justify-content: space-between;
  position: fixed;
  top: 0;
  z-index: 5;
  overflow: hidden;
  display: flex;
  justify-content: space-between;
  .logo {
    font-size: 0.34rem;
    color: white;
    text-align: left;
    line-height: 0.92rem;
    margin-left: 0.2rem;
  }
  div {
    font-size: 0.32rem;
    color: white;
    text-align: right;
    margin-right: 0.2rem;
    line-height: 0.92rem;
  }
  .el-icon-arrow-left {
    font-size: 0.4rem;
    color: white;
    line-height: 0.84rem;
    margin-left: 0.1rem;
  }
  .s1 {
    width: 3rem;
    font-size: 0.3rem;
    color: white;
    line-height: 0.84rem;
    text-align: center;
    margin-left: 0.3rem;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  .s2 {
    font-size: 0.28rem;
    color: white;
    line-height: 0.84rem;
    margin-right: 0.18rem;
  }
  .el-icon-search {
    font-size: 0.3rem;
    color: white;
    margin-left: 0.34rem;
    line-height: 0.88rem;
  }
  .el-icon-user {
    font-size: 0.3rem;
    color: white;
    line-height: 0.88rem;
    margin-right: 0.24rem;
  }
  .s3 {
    width: 3rem;
    font-size: 0.3rem;
    color: white;
    margin-right: 2.3rem;
    line-height: 0.88rem;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  .s4 {
    width: 3rem;
    font-size: 0.3rem;
    color: white;
    line-height: 0.88rem;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  .iconwodedangxuan {
    font-size: 0.34rem;
    color: white;
    margin-top: 0.25rem;
    margin-right: 0.3rem;
  }
  .edit {
    font-size: 0.28rem;
    color: white;
    margin-right: 0.2rem;
    margin-top: 0.24rem;
  }
}
</style>



